import React from 'react'
import { Container, Figure, Row, Image, Col } from 'react-bootstrap';
import bmw from '../pic/bmw.png'

const UpImage = () => {
    return (
        <div>
           <Container>
           <Figure>
                <Figure.Image
                    width={171}
                    height={180}
                    alt="171x180"
                    src={bmw}
                />
                <Figure.Caption>
                    Nulla vitae elit libero, a pharetra augue mollis interdum.
                </Figure.Caption>
                </Figure>
            </Container>
        </div>
    )
}

export default UpImage
